<template>
    <div class="alphabet">
        <ul class="list">
            <li class="item" 
                v-for="(item,index) in cites" 
                :key="index" 
                :ref="index"
                @click="handleLetter"
                @touchstart.prevent="handleTouchStart"
                @touchmove.prevent="handleTouchMove"
                @touchend.prevent="handleTouchEnd"
            >
                {{index}}
            </li>
        </ul>
        <span class="iconfont">^</span>
    </div>
</template>

<script>

export default {
    name:"CityAlphabet",
    data(){
        return {
            moveStatus:false,  //默认手指没按下
            Atop: 0 ,
            timer:null //默认延时器
        } 
    },
    props:{
        cites:Object
    },
    computed:{
        //将object拆分成字母数组形式
        //["A", "B", "C", "D", "E", "F", "G", "H", "J", "K", "L", "M", "N", "P", "Q", "R", "S", "T", "W", "X", "Y", "Z"]
        letters(){
            var arr = [];
            for(let key in this.cites){
                arr.push(key)
            }
            console.log(arr)
            return arr
        }
    },
    updated(){
        //生命周期---更新完毕就计算字母A距离div顶部的高度
        this.Atop = this.$refs['A'][0].offsetTop;
    },
    methods:{
        handleLetter(e){
            //通过点击获取点击的dom元素
            var letter = e.target.innerText;
            //console.log(letter)
            //将子组件的值传给父组件city
            this.$emit("letterHandle",letter)
        },
        handleTouchStart(){
            //手指触摸事件
            this.moveStatus = true
        },
        handleTouchMove(e){
            //手指移动事件
            if(this.moveStatus){
                //说明按下了，让城市列表移动

                if(this.timer){
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(()=>{
                    //1.获取手指在字母表拖拽时的坐标
                    var touchY = e.touches[0].clientY - 110; //110距离顶部的坐标

                    //2.计算字母A距离div顶部的高度
                    //var Atop = this.$refs['A'][0].offsetTop;

                    //字母的位置
                    var index = Math.floor((touchY-this.Atop)/20) //20为字母的行高

                    //判断出界
                    if(index >=0 && index < this.letters.length){
                        //根据计算的索引找到字母表数组中的字母
                        var letter = this.letters[index]
                        this.$emit("letterHandle",letter)
                    }
                },500)
            }
        },
        handleTouchEnd(){
            //手指离开事件
            this.moveStatus = false
        }

    }
}
</script>

<style scoped>
    .alphabet{
        /* display: flex;
        flex-direction: column;
        justify-content: center; */  
        position: absolute;
        right: 10px;
        top: 110px;
    }
    .list .item{
        line-height: 20px;
        color: #00bcd4;
    }
    .iconfont{
        
        color: red;
    }
</style>


