<template>
    <div class="home">
        <!-- 头部组件 -->
        <home-header></home-header>

        <!-- 轮播图组件 -->
        <home-swipper :list="swipeList"></home-swipper>

        <!-- 图片列表组件 -->
        <home-icon-list :list="iconlist"></home-icon-list>

        <!-- 热销推荐组件 -->
        <home-recommend :list="rexiaolist"></home-recommend>

        <!-- 周末去哪组件 -->
        <home-weekend :list="gotolist"></home-weekend>
    </div>
</template>

<script>
import {mapState} from 'vuex'
//引入axios使用发送请求
import axios from 'axios'
//引入HomeHeader组件
import HomeHeader from './components/Header'
//引入轮播图组件
import HomeSwipper from './components/Swipper'
//引入图标列表组件
import HomeIconList from './components/IconList'
//引入热销推荐组件
import HomeRecommend from './components/Recommend'
//引入周末去哪组件
import HomeWeekend from './components/Weekend'

export default {
    name:"Home",
    //注册组件
    components:{
        HomeHeader,
        HomeSwipper,
        HomeIconList,
        HomeRecommend,
        HomeWeekend
    },
    data(){
        return {
            //轮播图
            swipeList:[],
            //图标列表
            iconlist:[],
            //热销推荐
            rexiaolist:[],
            //周末去哪
            gotolist:[]

        }
    },
    mounted(){
        this.lastCity = this.city
        //组件加载完毕获取首页数据
        this.getHomeData();
    },
    computed:{
        //将state中的city属性映射到city这个属性
        ...mapState(['city'])
    },
    //当使用keep-alive组件时，会自动调用该函数
    activated(){
        //如果城市改变，就发送新的请求
        if(this.lastCity !== this.city){
            this.getHomeData(); //获取首页数据发送请求
            this.lastCity = this.city //修改城市
        }
    },
    methods:{
        //获取首页数据
        getHomeData(){
            axios.get('/tour/static/api/index.json')
            .then((res)=>{
                //发送成功
                console.log(res.data.data)
                this.swipeList = res.data.data.swiperList;
                this.iconlist = res.data.data.iconList;
                this.rexiaolist = res.data.data.recommendList;
                this.gotolist = res.data.data.weekendList;
            })
        }
    }
}
</script>

<style scoped>

</style>


