<template>
    <div class="weekend">
        <h2 class="title">周末去哪儿</h2>
        <div class="content-list" v-for="item in list" :key="item.id">
            <div class="content">
                <img :src="item.imgUrl" alt="">
            </div>
            <div class="title">
                {{item.title}}
            </div>
            <div class="desc">
                {{item.desc}}
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeWeekend",
    props:{
        list:Array
    }
}
</script>

<style scoped>
    .weekend > .title{
        height: 40px;
        line-height: 40px;
        background-color: #ccc;
        padding-left: 5px;
    }
    .title {
        font-size: 16px;
    }
    .desc{
        color: #ccc;

    }
    .content img{
        width: 100%;
    }
    .content-list > .title,.desc{
        padding-left: 5px;
        margin: 5px 0;
    }
</style>


