<template>
    <div class="swipper">
        <mt-swipe :auto="4000">
        <mt-swipe-item   v-for="item of list" :key="item.id">
            <a href="">
                <img :src="item.imgUrl" alt="">
            </a>
        </mt-swipe-item>
        </mt-swipe> 
    </div>
</template>

<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

export default {
    name:"HomeSwipper",
    props:{
        //接收Home父组件传递的数组
        list:Array
    }
}
</script>

<style scoped>
    .swipper{
        height: 200px;
    }
    .swipper img{
        width: 100%;
        height:100%;
    }
</style>


