import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'
Vue.use(Vuex)

 
//初始化Vuex.Store
export default new Vuex.Store({

    //state,用来保存多个数据共享
    //state:state,  //健和值都一样，可以简化
    state,
    //用来修改数据
    //mutations:mutations
    mutations
})


