<template>
    <div class="city">
        <!-- 城市选择头部组件 -->
        <city-header></city-header>
        <!-- 城市搜索组件 -->
        <city-search :cites="cites"></city-search>
        <!-- 城市列表组件 -->
        <city-list :cites="cites" :hot="hotCities" :letter="letter"></city-list>
        <!-- 字母表组件 -->
        <city-alphabet :cites="cites" @letterHandle="service"></city-alphabet>
    </div>
</template>

<script>
import axios from 'axios'
//引入城市选择头部组件
import CityHeader from './components/Header'
//引入搜索组件
import CitySearch from './components/Search'
//引入城市列表
import CityList from './components/CityList'
//引入字母表
import CityAlphabet from './components/Alphabet'

export default {
    name:"City",
    components:{
        CityHeader,
        CitySearch,
        CityList,
        CityAlphabet
    },
    data(){
        return {
            cites:{},
            hotCities:[],
            letter:""
        }
    },
    mounted(){
        this.getCityInfo();
    },
    methods:{
        getCityInfo(){
            axios.get("/tour/static/api/city.json")
            .then(this.handleCityInfo)
        },
        handleCityInfo(res){
            res = res.data;
            console.log(res)
            if(res){
                const data = res.data;
                this.cites = data.cities
                this.hotCities = data.hotCities
            }
        },
        service(letter){
            this.letter = letter
        }
    }
}
</script>

<style scoped>
    
</style>


