<template>
    <div class="city-list"  ref="wrapper">
        <div>
            
            <!-- 当前城市 -->
            <div class="current">
                <h2 class="title">当前城市</h2>
                <div class="button-list">
                    <div class="button-wrapper">
                        <div class="button">
                            <!-- {{this.$store.state.city}} 简化成下面 -->
                            {{currentCity}}
                        </div>
                    </div>
                </div>
            </div>

            <!-- 热门城市 -->
            <div class="hot">
                <h2 class="title">热门城市</h2>
                <div class="button-list">
                    <div class="button-wrapper" v-for="(item,index) in hot" :key="index" @click="handleChangeCity(item.name)">
                        <div class="button">{{item.name}}</div>
                    </div>
                </div>
            </div>

            <!-- 城市列表 -->
            <div class="list" v-for="(item,index) in cites" :key="index" :ref="index" >
                <h2 class="title">{{index}}</h2>
                <ul v-for="(city,index) in item" :key="index" @click="handleChangeCity(city.name)">
                    <li class="item">{{city.name}}</li>
                </ul>
            </div>
            
        </div>
    </div>
</template>

<script>

//引入mapstate进行获取共享数据优化
// import {mapState} from 'vuex'
import {mapState,mapMutations} from 'vuex'
import BScroll from 'better-scroll'
export default {
    name:"CityList",
    props:{
        cites:Object,
        hot:Array,
        letter:String
    }, 
    computed:{
        //将state中的city映射到计算属性的currentCity
        ...mapState({currentCity:"city"})
    },
    mounted(){
        let wrapper = this.$refs.wrapper;
        this.scroll = new BScroll(wrapper)
    },
    watch:{
        //侦听letter数据项，一旦改变，处理
        letter(){
            //计算目标位置
            //console.log(this.$refs[this.letter][0])

            this.scroll.scrollToElement(this.$refs[this.letter][0])
        }
    },
    methods:{
        handleChangeCity(city){
            //向store提交mutations
            //this.$store.commit("changeCity",city) 简化成下面
            this.changeCity(city)
            this.$router.push({path:"/"})
        },
        //将mutations中的changeCity映射到methods的changeCity方法中
        ...mapMutations(['changeCity'])

    }
}
</script>

<style scoped>
    .title{
        line-height: 30px;
        background-color: #eee;
    }
    .button-list{
        display: flex; /*默认都显示在一行*/
        flex-wrap: wrap; /*换行显示*/
        
    }
    .button-wrapper{
        width: 33.33%;
        
    }
    .button{
        border: 1px solid #ccc;
        border-radius: 5px;
        box-sizing: border-box;
        text-align: center;
        padding: 5px 0;
        margin: 5px 15px;
    }
    .item{
        line-height: 35px;
        padding-left: 10px;
    }
    .city-list{
        position: absolute;
        left: 0;
        right: 0;
        top: 80px;
        bottom: 0;
        z-index: -999;
        
    }
</style>


