<template>
    <div class="header">
        <div class="left">
            <span class="iconfont">&#xe64e;</span>
        </div>
        <div class="search">
            <span class="iconfont">&#xe7c4;</span>
            请输入城市/景点/游玩主题
        </div>
        <div class="right">
            <router-link to="/city">
                {{this.$store.state.city}}
                <span class="iconfont">&#xe6f0;</span>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name:"HomeHeader"
}
</script>

<style scoped>
    .header{
        display: flex; /*flex布局*/
        justify-content: space-between;/*两端对齐*/
        height: 45px;
        color:#fff;
        background-color: #00bcd4;
        line-height: 45px;
    }
    .search{
        flex:1;  /*占领剩余空间*/
        color: #ccc;
        background-color: #fff;
        border-radius: 5px;
        margin: .08rem;
        padding-left: .16rem;
        
    }
    span{
        color:black;
        line-height: .853333rem;
    }
    .right a{
        color: #fff;
    }
    .iconfont{
        color: #fff;
    }
</style>


