<template>
    <div>
        <div class="city-search">
            <input v-model="keyword" type="text" class="search-input" placeholder="请输入城市名或拼音" >
        </div>
        <div class="search-content" v-show="keyword">
            <ul>
                <li class="item" v-for="item in list" :key="item.id" @click="handleChangeCity(item.name)">
                    {{item.name}}
                </li>
                <!-- <li class="item" v-show="hasNoData">
                    没有找到匹配数据
                </li> -->
            </ul>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapMutations} from 'vuex'

export default {
    name:"CitySearch",
    props:{
        cites:Object
    },
    data(){
        return {
            keyword:"",
            list:[],
            timer:null,
           //hasNoData:false
        }
    },
    watch:{
        //侦听搜索框是否发生改变
        keyword(){
            var result = [];
            if(this.timer){
                clearTimeout(this.timer)
            }
            // if(this.keyword == ""){
            //     this.hasNoData = true
            // }
            this.timer = setTimeout(() => {
                //遍历sities对象：{A：{}，B{}}
                for(let i in this.cites){
                    //console.log(i)
                    //根据下标获取对应的数组：A{。。}
                    this.cites[i].forEach( value => {
                        if(value.spell.indexOf(this.keyword)> -1 || value.name.indexOf(this.keyword)> -1){
                            result.push(value)
                        }
                    });

                }
                this.list = result
            }, 500);
        }
    },
    methods:{
        handleChangeCity(city){
            //向mutations提交修改城市的事件
            //this.$store.commit("changeCity",city)
            this.changeCity(city)
            //修改之后动态路由跳转首页
            this.$router.push({path:"/"})
        },
        //将mutations中的changeCity映射到methos的changeCity
        ...mapMutations(['changeCity'])
    }
}
</script>

<style scoped>
    .city-search{
        height: 40px;
        background-color: #00bcd4;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .search-input{
        height: 30px;
        border: 1px solid #eee;
        border-radius: 5px;
        width: 90%;
        text-align: center;
        box-sizing: border-box;
    }

    .city-search{
        height: 30px;
        padding: 5px;
        background: #00bcd4;
    }
    .search-content{  /* 搜索列表 */
        position: absolute;
        background-color: #eee;
        left: 0;
        right: 0;
        top: 80px;
        bottom: 0;
    }
    .item{
        line-height: 35px;
        padding-left: 10px;
        background: #fff;
        color: #666;
    }
</style>


