<template>
    <div class="icon-list">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(page,index) of pages" :key="index">
                <div class="icon" v-for="item of page" :key="item.id">
                    <div class="icon-img">
                        <img :src="item.imgUrl" alt="">
                    </div>
                    <div class="icon-desc">
                        {{item.desc}}
                    </div>
                </div>
            </mt-swipe-item>
        </mt-swipe> 
    </div>
</template>

<script>

export default {
    name:"HomeIconList",
    computed:{
        //定义计算属性，计算图片列表几平展示
        pages(){
            let pages = [];
            this.list.forEach((value,index)=>{
                // 0-7，之间的元素，放在下标为0的数组中
                // 8-15，之间的元素，放在下标为1的数组中
                var page = Math.floor(index/8);  
                if(!pages[page]){
                    pages[page] = [];
                }
                pages[page].push(value);
                console.log(pages)
            })
            return pages;
        }
    },
    props:{
        //接收Home父组件传递的数据
        list:Array
    }
}
</script>

<style scoped>
    .icon-list >>> .mint-swipe-indicator{
        opacity: 0.9;
        
    }
    .icon-list >>> .mint-swipe-indicator .is-active{
       
        background: #000;
    }
    .icon-list{
        height:250px;
        text-align: center;
    }
    .icon{
        float: left;
        width: 25%;
        box-sizing: border-box;
        padding: 10px;
    }
    .icon-img img{
        width: 100%;
    }
    .icon-desc{
        margin-top: 10px;
    }
</style>


